import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Index from "@/components/route/index"
import Match from "@/components/route/match"
import Literacy from "@/components/route/literacy"
import Community from "@/components/route/community"
import More from "@/components/route/more"
import Plate from "@/components/route/include/plate"
import Recom from "@/components/route/include/recom"
import Video from "@/components/route/include/video"

Vue.use(Router)

export default new Router({
    routes: [{
            path: "/",
            redirect: "HelloWorld/literacy"
        },
        {
            path: '/',
            name: 'HelloWorld',
            component: HelloWorld,
            children: [{
                path: "/HelloWorld/index",
                component: Index
            }, {
                path: "/HelloWorld/match",
                component: Match
            }, {
                path: "/HelloWorld/literacy",
                component: Literacy
            }, {
                path: "/HelloWorld/community",
                component: Community,
                children: [{
                    path: "/HelloWorld/community/plate",
                    component: Plate
                }, {
                    path: "/HelloWorld/community/recom",
                    component: Recom
                }, {
                    path: "/HelloWorld/community/video",
                    component: Video
                }]
            }, {
                path: "/HelloWorld/more",
                component: More
            }]
        }
    ],
    linkActiveClass: "active"
})


