import Vue from 'vue'
import App from './App'
import router from './router'
import icon from '../static/icon/iconfont.css'
import Header from './components/common/header'

window.bus = new Vue()

Vue.component("divHeader", Header)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    render: h => h(App)
})


