<template>
  <div class="rightBox">
    <dl v-for="item in rightList" @click="alert(item)">
        <dt>
            <img :src="item.picurl" class="img-res" style="width:100%">
        </dt>
        <dd>
            <p v-text="item.font"></p>
            <p v-text="item.talk"></p>
        </dd>
    </dl>
  </div>
</template>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<script>

export default {
  name: 'HelloWorld',
  data () {
    return {
        title:"",
       rightList:[] 
    }
  },
  mounted(){
      bus.$on("rightdata",res=>{
            this.title = res.title
          this.rightList = res.data
      })
  },
  methods:{
      alert(item){
          console.log(this.$route)
          alert("社区->版块->"+this.title+"->"+item.font)
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.rightBox{
    width:100%;
    display:flex;
    flex-wrap:wrap;
}
dl{
   width:33%;
}
</style>


