<template>
  <div clss="list mt15">
    <ul class="col-xs-4 pl0 pr0">
        <li v-for="(item,index) in dataList" @click="send(item,index)" :class="{addclass:sum==index}">{{item.title}}</li>
    </ul>
    <div class="divwrap col-xs-8">
        <right></right>
    </div>
  </div>
</template>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<script>
import axios from "axios"
import Right from "../../renderData/right"
export default {
  name: 'HelloWorld',
  data () {
    return {
      dataList:[],
      sum:0
    }
  },
   mounted(){
    this.getInfo()
  },
   methods:{
    getInfo(){
        axios.get("/api").then(res=>{
          this.dataList = res.data.data       
        })      
    },
    send(item,index){
      this.sum = index
      console.log(item.title)
      bus.$emit("rightdata",{
          title:item.title,
          data:item.content
      })
     // console.log(item.content)
    }
  },
  components:{
    Right
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul{
  height:100%;
  background:#ccc;
}
li{
  width:100%;
  line-height:46px;
  text-align:center;
}
.addclass{
  background:#fff;
  color:red;
}
</style>


